import * as React from "react"
import Main from "../components/Main"
import { graphql } from 'gatsby';
import "./index.css"

const Home = ({ data }) => {
  return (
    <>
    {/* <div className="body"> */}
      <Main data={data} />
      {/* </div> */}
    </>
  );
};

export const query = graphql`
  query {
    allMarkdownRemark {
      nodes {
        html
        frontmatter {
          date

        }
      }
    }
  }
`;

export default Home;
